/* src/components/banner/facts/releaseDate/ReleaseDate.css */

*, ::before, ::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.ReleaseDate{
    display: flex;
    align-items: center;
    text-align: center;

    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.release-date-value{
    font-size: 18px;
    font-weight: 400;
    color: #FFFFFF;

    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}