/* src/components/banner/Banner.css */

*, ::before, ::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.Banner{
    background-size: cover;
    background-position: top center;
    height: 656px;
    margin-bottom: 15px;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.banner-content{
    margin-left: 80px;
    padding-top: 140px;
}
.banner-content .facts{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.facts-separator{
    margin: 0 14px;
}
.facts-separator-element{
    height: 3.4px;
    width: 3.4px;
    border-radius: 50%;
    background-color: #666666;
    border: none;
}

.banner-title{
    font-family: Helvetica, sans-serif;
    letter-spacing: 10px;
    word-spacing: 20px;
    color: white;
    font-size: 2.5rem;
    font-weight: lighter;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);

    margin-top: 1rem;
    padding-bottom: 0.3rem;
}

.banner-buttons-group{
    padding-top: 1rem;
}
.banner-button{
    cursor: pointer;
    transition-duration: 0.4s;
    color: #FFFFFF;
    outline: none;
    border: none;
    font-weight: 700;

    padding: 12px 25px;
    border-radius: 20px;

    margin-right: 15px;

    position: relative;
}

.button-watch{
    background-color: #E50914;
}
.button-watch:hover{
    color: #111111;
    box-shadow: 0px 0px 10px 1px rgba(5, 5, 5, 1);
    transform: scale(1.02);
}

.button-addToList{
    background-color: rgba(51, 51, 51, 0.5);
}
.button-addToList:hover{
    color: #111111;
    background-color: #E50914;
    box-shadow: 0px 0px 10px 1px rgba(5, 5, 5, 1);
    transform: scale(1.02);
}
.bb-button{
    display: flex;
    justify-content: space-around;
}
.buttonName{
    font-size: 16px;
    line-height: 1;
    margin-left: 5px;
}
.bb-icon-watch{
    max-width: 12px;
    height: 100%;
    margin-right: 5px;
}
.banner-button .bb-icon-watch-hover-image{
    display: none;
}
.banner-button:hover .bb-icon-watch{
    display: none;
}
.banner-button:hover .bb-icon-watch-hover-image{
    display: inline-block;
    max-width: 12px;
    height: 100%;
    margin-right: 5px;
}

.bb-icon-addToList{
    max-width: 15px;
    height: 100%;
    margin-right: 10px;
}
.banner-button .bb-icon-addToList-hover-image{
    display: none;
}
.banner-button:hover .bb-icon-addToList{
    display: none;
}
.banner-button:hover .bb-icon-addToList-hover-image{
    display: inline-block;
    max-width: 15px;
    height: 100%;
    margin-right: 10px;
}

.banner-description{
    color: white;
    padding-top: 1rem;
    font-size: 1.2rem;
    width: 40%;
    max-width: 700px;

    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.banner-fade-bottom{
    width: 100%;
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}