/* src/components/Navbar.css */

*, *::before, *::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.NavBar{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}
.NavBar-logo{
    width: 100px;
    object-fit: contain;
    cursor: pointer;
    transition: 0.7s;
}
.NavBar-logo:hover{
    box-shadow: 0px 0px 10px 1px rgba(5, 5, 5, 0);
    transform: scale(1.1);
    opacity: 1;
}

.NavBar-avatar{
    width: 30px;
    object-fit: contain;
    cursor: pointer;
    transition: 0.7s;
}
.NavBar-avatar:hover{
    box-shadow: 0px 0px 10px 1px rgba(5, 5, 5, 0);
    transform: scale(1.1);
    opacity: 1;
}