/* src/components/banner/facts/starRating/StarRating.css */

*, ::before, ::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.star-rating{
    display: flex;
    align-items: center;
}
.star-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #e50914;
    width: 18px;
    margin-right: 10px;
}
.icon-img{
    width: 100%;
}
.rating-value{
    font-size: 18px;
    font-weight: 400;
    color: #FFFFFF;

    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}