/* src/components/row/RowPost.css */

/* constant values: use only where the value should be same with another attribute*/
:root{
    --margin_05: 5px;
    --margin_10: 10px;
    --margin_15: 15px;
    --margin_20: 20px;

    --font-height_20: 20px;
    --font-height_25: 25px;

    --border_radius_05: 5px;
    --border_radius_10: 10px;

    --width_70: 70px;
}

*, ::before, ::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.RowPost{
    margin-left: var(--margin_15);
    color: #FFFFFF;
}
.title{
    margin-left: var(--margin_20);
    padding-bottom: 5px;

    font-family: Helvetica, sans-serif;
    color: #FFFFFF;
    font-size: 1.5rem;
    font-weight: lighter;
}
.posters{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;

    margin-left: var(--margin_15);
    padding: 10px 0 25px 0;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    height: 250px;

    transition: 450ms;
}
.poster:hover{
    transform: scale(1.02);
    opacity: 1;
}
.small-posters{
    height: 150px;
    
    transition: 450ms;
}
.small-posters:hover{
    transform: scale(1.02);
    opacity: 1;
}

.poster-container{
    cursor: pointer;
    position: relative;
    display: inline-block;

    margin-left: var(--margin_05);
    margin-right: var(--margin_05);

    transition: 450ms;
}
.poster-title-container{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;

    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin: 0 10px 10px 10px;
    min-height: min-content;

    transition: 450ms;
}
.poster-title{
    font-family: Helvetica, sans-serif;
    color: #FFFFFF;
    font-size: 25px;
    letter-spacing: 3px;
    font-weight: 500;
    text-shadow: 2px 2px 7px rgba(0, 0, 0, 1);
}
.poster-title-long{
    font-family: Helvetica, sans-serif;
    font-size: 20px;
    letter-spacing: 3px;
    font-weight: 500;
    text-shadow: 2px 2px 7px rgba(0, 0, 0, 1);
    margin-bottom: 5px;
}
.poster-container:hover{
    transform: scale(1.02);
    opacity: 1;
}
.poster-container:hover .poster-title-container{
    transform: scale(1.02);
    opacity: 1;
}

/* styles for Modal */
.modal{
    height: initial;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: var(--border_radius_10);
}
.modal-container{
    margin: 0;
    padding: 0;
    background-color: none;
    box-shadow: 0 2px 10px rgb(145, 145, 145), 0 2px 5px rgba(145, 145, 145, 0.8);
    border-radius: var(--border_radius_10);
}
.modal-head{
    display: flex;
    justify-content: space-between;
    align-items: center;

    background-color: #282828;
    border-radius: var(--border_radius_10) var(--border_radius_10) 0 0;

    padding-left: 15px;
}

.modal-head-logo{
    width: var(--width_70);
    object-fit: contain;
    cursor: pointer;
    transition: 0.7s;
}
.modal-head-logo:hover{
    box-shadow: 0px 0px 10px 1px rgba(5, 5, 5, 0);
    transform: scale(1.1);
    opacity: 1;
}

.modal-title{
    font-family: Helvetica, sans-serif;
    color: #FFFFFF;
    font-size: var(--font-height_20);
    font-size: 20px;
    font-weight: lighter;
    letter-spacing: 7px;
    padding: 10px 15px 5px 15px;
}

.close-modal{
    width: var(--width_70);
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.icon-close-modal{
    cursor: pointer;
    max-width: 15px;
    height: 100%;
    margin-right: 15px;
    border-radius: 50%;

    transition: 0.5s ease;
}
.icon-close-modal:hover{
    cursor: pointer;
    box-shadow: 0px 0px 10px 3px rgba(1, 1, 1, 1);
    transform: scale(1.2);
}

.modal-video{
    margin: 0;
    padding: 0;
    border-radius: 0 0 var(--border_radius_10) var(--border_radius_10);
}
.youtube-video{
    border-bottom-left-radius: var(--border_radius_10);
    border-bottom-right-radius: var(--border_radius_10);
    overflow: hidden;
    z-index: 1;

    margin: 0;
    padding: 0;
    justify-self: flex-end;
}